.wrapper{
    background: #baf8a7;
    height: 30vh;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.container{
    padding: 10px 0;
    width: 80%;
    border: 1px solid;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.footer{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
}

.footerImgs{
    width: 50px;
    height: 50px;
    background: gray;
}